/* eslint-disable prettier/prettier */
import React, { useState, useEffect } from "react"
import { useCookies } from "react-cookie"
import Helmet from "react-helmet"
import "./reset.css"
import { PropTypes } from "prop-types"
import Header from "./header/header"
import Footer from "./footer/footer"
import Intro from "./intro/intro"
import VisibilityContext from "../context/visibility"

const Layout = ({ children }) => {
    const cookieConfig = {
        name: "intro",
        timeout: 3600,
    }
    const bodyVisibilityTimeout = 1800
    const [playIntro, setPlayIntro] = useState(false)
    const [bodyVisibility, setBodyVisibility] = useState(false)
    const [cookies, setCookie] = useCookies([cookieConfig.name])
    const unmountIntro = () => {
        setPlayIntro(false)
        setBodyVisibility(true)
    }

    useEffect(() => {
        if (!cookies[cookieConfig.name]) {
            setPlayIntro(true)
            setCookie(cookieConfig.name, true, {
                maxAge: cookieConfig.timeout,
            })
        } else {
            setBodyVisibility(true)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            <Helmet>
                <title>James Maclennan - Front End Web Developer</title>
                <link
                    href="https://fonts.googleapis.com/css?family=Roboto|Lato"
                    rel="stylesheet"
                />
            </Helmet>
            <VisibilityContext.Provider value={bodyVisibility}>
                {playIntro ? (
                    <Intro
                        bodyVisibilityTimeout={bodyVisibilityTimeout}
                        unmountIntro={unmountIntro}
                    />
                ) : null}
                <div style={{ opacity: bodyVisibility ? 1 : 0 }}>
                    <Header />
                    <main>{children}</main>
                    <Footer />
                </div>
            </VisibilityContext.Provider>
        </>
    )
}

export default Layout

Layout.defaultProps = {
    children: "<div/>",
}

Layout.propTypes = {
    children: PropTypes.node,
}
