import React, { useEffect } from 'react'
import { useSpring, animated } from 'react-spring'
import PropTypes from 'prop-types'
import BodyClassName from 'react-body-classname'
import Styles from './intro.module.css'
import Title from '../header/title'

const Intro = ({ unmountIntro, bodyVisibilityTimeout }) => {
	const titleAnimationOffset = -50
	const containerAnimation = useSpring({
		to: {
			opacity: 1,
		},
	})

	const titleAnimation = useSpring({
		from: {
			transform: `translateY(0px)`,
			opacity: 0,
		},
		to: {
			transform: `translateY(${titleAnimationOffset}px)`,
			opacity: 1,
		},
		config: { mass: 1, tension: 100, friction: 20 },
	})

	useEffect(() => {
		setTimeout(() => {
			unmountIntro()
		}, bodyVisibilityTimeout)
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [])

	return (
		<BodyClassName className={Styles.isLocked}>
			<animated.div
				className={Styles.container}
				style={containerAnimation}
			>
				<animated.div style={titleAnimation}>
					<Title />
				</animated.div>
			</animated.div>
		</BodyClassName>
	)
}

Intro.defaultProps = {
	bodyVisibilityTimeout: 1800,
}

Intro.propTypes = {
	unmountIntro: PropTypes.func.isRequired,
	bodyVisibilityTimeout: PropTypes.number,
}

export default Intro
